// ===================== src/components/auth/Login.js =====================
import React, { useState } from "react";
import { useAuth } from "../../context/AuthContext";
import { useNavigate, useSearchParams } from "react-router-dom";

export default function Login() {
  const { login } = useAuth();
  const navigate = useNavigate();
  const [params] = useSearchParams();
  const next = params.get("next") || "/";

  const [form, setForm] = useState({ email: "", password: "" });
  const [submitting, setSubmitting] = useState(false);
  const [err, setErr] = useState("");

  const onSubmit = async (e) => {
    e.preventDefault();
    setErr("");

    if (!form.email || !form.password) {
      setErr("Please fill all required fields.");
      return;
    }

    try {
      setSubmitting(true);

      await Promise.resolve(
        login({
          email: form.email.trim(),
          password: form.password,
        })
      );

      setForm({ email: "", password: "" });
      navigate(next, { replace: true });
    } catch (error) {
      setErr(error?.message || "Login failed. Please try again.");
    } finally {
      setSubmitting(false);
    }
  };

  return (
    <form onSubmit={onSubmit} className="space-y-4">
      {err && (
        <div className="p-3 rounded-lg bg-red-50 text-red-700 text-sm">
          {err}
        </div>
      )}

      <div>
        <label className="block text-sm font-medium mb-2">Email Address</label>
        <input
  type="email"
  value={form.email}
  onChange={(e) => setForm({ ...form, email: e.target.value })}
  className="auth-input w-full px-4 py-3 rounded-lg focus:outline-none"
  placeholder="your@email.com"
  autoComplete="email"
/>
      </div>

      <div>
        <label className="block text-sm font-medium mb-2">Password</label>
        <input
  type="password"
  value={form.password}
  onChange={(e) => setForm({ ...form, password: e.target.value })}
  className="auth-input w-full px-4 py-3 rounded-lg focus:outline-none"
  placeholder="Your password"
  autoComplete="current-password"
/>
      </div>

      <button
  type="submit"
  disabled={submitting}
  className={`auth-btn w-full bg-gradient-to-r from-orange-500 to-red-500 text-white py-3 rounded-lg font-medium transition ${
    submitting
      ? 'opacity-70 cursor-not-allowed'
      : 'hover:from-orange-600 hover:to-red-600'
  }`}
>
  {submitting ? 'Logging in…' : 'Login'}
</button>

    </form>
  );
}
// ===================== end of src/components/auth/Login.js =====================
