import React, { useState } from "react";
import { useAuth } from "../../context/AuthContext";
import "./register.css";

export default function Register() {
  const { register } = useAuth();
  const [form, setForm] = useState({
    name: "",
    email: "",
    phone: "",
    password: "",
    confirm: "",
  });
  const [submitting, setSubmitting] = useState(false);
  const [err, setErr] = useState("");

  const onSubmit = (e) => {
    e.preventDefault();
    setErr("");

    if (!form.name || !form.email || !form.password) {
      setErr("Please fill all required fields.");
      return;
    }
    if (form.password.length < 6) {
      setErr("Password should be at least 6 characters.");
      return;
    }
    if (form.password !== form.confirm) {
      setErr("Passwords do not match.");
      return;
    }

    setSubmitting(true);
    try {
      register({
        name: form.name.trim(),
        email: form.email.trim(),
        password: form.password,
        phone: form.phone.trim(),
      });
    } finally {
      setSubmitting(false);
    }
  };

  return (
    // form gets the hook class so register.css can style it
    <form onSubmit={onSubmit} className="register-form">
      {err && <div className="error-box">{err}</div>}

      <div>
        <label className="block text-sm font-medium mb-2">Full Name</label>
        <input
          value={form.name}
          onChange={(e) => setForm({ ...form, name: e.target.value })}
          className="auth-input w-full px-4 py-3 rounded-lg"
          placeholder="Your full name"
          autoComplete="name"
        />
      </div>

      <div>
        <label className="block text-sm font-medium mb-2">Email Address</label>
        <input
          type="email"
          value={form.email}
          onChange={(e) => setForm({ ...form, email: e.target.value })}
          className="auth-input w-full px-4 py-3 rounded-lg"
          placeholder="your@email.com"
          autoComplete="email"
        />
      </div>

      <div>
        <label className="block text-sm font-medium mb-2">Phone Number</label>
        <input
          type="tel"
          value={form.phone}
          onChange={(e) => setForm({ ...form, phone: e.target.value })}
          className="auth-input w-full px-4 py-3 rounded-lg"
          placeholder="+91 99********"
          autoComplete="tel"
        />
      </div>

      <div>
        <label className="block text-sm font-medium mb-2">Password</label>
        <input
          type="password"
          value={form.password}
          onChange={(e) => setForm({ ...form, password: e.target.value })}
          className="auth-input w-full px-4 py-3 rounded-lg"
          placeholder="New password"
          autoComplete="new-password"
        />
      </div>

      <div>
        <label className="block text-sm font-medium mb-2">Confirm Password</label>
        <input
          type="password"
          value={form.confirm}
          onChange={(e) => setForm({ ...form, confirm: e.target.value })}
          className="auth-input w-full px-4 py-3 rounded-lg"
          placeholder="Confirm password"
          autoComplete="new-password"
        />
      </div>

      <button
        type="submit"
        disabled={submitting}
        className={`auth-btn w-full bg-gradient-to-r from-orange-500 to-red-500 text-white py-3 rounded-lg font-medium transition ${
          submitting ? "opacity-70 cursor-not-allowed" : "hover:from-orange-600 hover:to-red-600"
        }`}
      >
        {submitting ? "Creating..." : "Create Account"}
      </button>
    </form>
  );
}
